import React from 'react';

const Mobile = React.createContext();

//=================================================================================
//=================================================================================

    class Graph extends React.Component {
        constructor(props) {
            super(props);
            this.state = {
                'nodes': [],
                'edges': [],
                'crosshair': {
                    'enable': true,
                    'display': false,
                    'top': 0,
                    'left': 0
                },
                'colorSeed': [
                    [1867, 4931, 6073],
                    131,
                    110,
                    80
                ],
                'mobile': false,
                'scale': 2.7
            }
        }

        addNode(position = [0, 0]) {
            this.setState((state) => {
                state.nodes.push({
                    left: position[0] > 0? position[0] : 0,
                    top: position[1] > 0? position[1] : 0
                });

                return state;
            });
        }

        addEdge(v1, v2) {
            if (this.state.nodes[v1] && this.state.nodes[v2] && v1 !== v2) {
                this.setState((state) => {
                    state.edges.push([v1, v2]);

                    return state;
                });
                return 'success';
            }

            throw Error('Саша ИЗВИНИ');
        }

        nodeUpdate = (index, newPosition) => {
            this.setState((state) => {
                state.nodes[index].left = newPosition[0];
                state.nodes[index].top = newPosition[1];

                return state;
            });
        }

        nodeDelete = (index) => {
            this.setState((state) => {
                state.edges.forEach((edge, indexEdge) => {
                if (edge[0] === index || edge[1] === index)
                    state.edges[indexEdge] = null;
                })

                let i = 0
                while (i < state.edges.length) {
                    if (state.edges[i] === null) state.edges.splice(i, 1); else i++;
                }

                state.nodes[index] = null;
                state.crosshair.display = false;

                return state;
            });
        }

        edgeDelete = (index) => {
            this.setState((state) => {
                state.edges.splice(index, 1);

                return state;
            });
        }

        crosshair = (crosshairState, x, y) => {
            this.setState((state) => {
                if (x) state.crosshair.left = x;
                if (y) state.crosshair.top = y;
                state.crosshair.display = crosshairState;   

                return state;
            });
        }

        paintAlgo = () => {
            const colorSeed = this.state.colorSeed;

            let nodes = [],
                nodesCount = this.state.nodes.length,
                edges = this.state.edges;
            let matrix = new Array(nodesCount).fill(0);
            matrix.forEach((_, i) => matrix[i] = new Array(nodesCount));

            edges.forEach((edge) => {
                matrix[edge[0]][edge[1]] = 1;
                matrix[edge[1]][edge[0]] = 1;
            })

            this.state.nodes.forEach((node, i) => {
                if (node) {
                    nodes.push(node);
                    let n = 0;

                    for (let k = 0; k < nodesCount; k++)
                        if (matrix[i][k] === 1) n++;

                    nodes[nodes.length - 1].color = null;
                    nodes[nodes.length - 1].index = i;
                    nodes[nodes.length - 1].n = n;
                }
            });

            for (let i = 0; i < nodes.length; i++) {
                let max = i;

                for (let j = i + 1; j < nodes.length; j++)
                    if (nodes[max].n < nodes[j].n) max = j;

                [nodes[i], nodes[max]] = [nodes[max], nodes[i]];
            }

            let color = 1, painted = 0;
            
            while (painted < nodes.length) {
                painted = 0;

                for (let i = 0; i < nodes.length; i++) {
                    if (!nodes[i].color) {
                        let freeNode = true;

                        for (let j = 0; j < nodes.length; j++) {
                            if (nodes[j].color && nodes[j].color === color && matrix[nodes[i].index][nodes[j].index] === 1) {
                                freeNode = false;
                                break;
                            }
                        }

                        if (freeNode) nodes[i].color = color;
                    } else painted++;
                }

                color++;
            }

            nodes.forEach((node, i) => {
                nodes[i].color = [
                    colorSeed[0][0] * node.color % colorSeed[1] + colorSeed[2], 
                    colorSeed[0][1] * node.color % colorSeed[1] + colorSeed[2], 
                    colorSeed[0][2] * node.color % colorSeed[1] + colorSeed[2]
                ];
                nodes[i].textColor = [
                    Math.min(nodes[i].color[0] + colorSeed[3], 255), 
                    Math.min(nodes[i].color[1] + colorSeed[3], 255), 
                    Math.min(nodes[i].color[2] + colorSeed[3], 255)
                ];
            });
            this.setState({nodes: this.state.nodes});

            return color - 2;
        }

        colorSeedUpdate = (newColorSeed) => {
            this.setState({colorSeed: newColorSeed});
            this.paintAlgo();
        }

        drag = (e, target, _this, callback, crosshairState = false) => {
            const TouchEndEvent = document.ontouchend;
            
            if (this.state.mobile !== true) target.style.cursor = 'grabbing';
            let crosshairPos = [0, 0],
                scale = 1.0;

            if (this.state.mobile === true) scale *= this.state.scale;

            if (crosshairState) {
                crosshairPos[0] = target.offsetLeft + target.clientWidth / 2 - 0.8 * scale;
                crosshairPos[1] = target.offsetTop + target.clientHeight / 2 - 0.8 * scale;
                this.crosshair(true, crosshairPos[0], crosshairPos[1], target);
            }

            if (this.state.mobile === true) {
                const pointerPos = [
                    e.touches[0].pageX - target.offsetLeft,
                    e.touches[0].pageY - target.offsetTop
                ];

                document.ontouchmove = (e) => {
                    e.preventDefault();
                    let pos = [e.touches[0].pageX - pointerPos[0], e.touches[0].pageY - pointerPos[1]];
                    if (pos[0] < 0) pos[0] = 0;
                    if (pos[1] < 0) pos[1] = 0;
                    crosshairPos[0] = pos[0] + target.clientWidth / 2 - 0.8 * scale;
                    crosshairPos[1] = pos[1] + target.clientHeight / 2 - 0.8 * scale;
                    if (crosshairState) this.crosshair(true, crosshairPos[0], crosshairPos[1]);
                    callback(_this, pos);
                }

                document.ontouchend = (e) => {
                    if (crosshairState) this.crosshair(false);
                    document.ontouchmove = () => {}
                    document.ontouchend = TouchEndEvent;
                }
            } else {
                const pointerPos = [
                    e.pageX - target.offsetLeft,
                    e.pageY - target.offsetTop
                ];

                document.onmousemove = (e) => {
                    let pos = [e.pageX - pointerPos[0], e.pageY - pointerPos[1]];
                    if (pos[0] < 0) pos[0] = 0;
                    if (pos[1] < 0) pos[1] = 0;
                    crosshairPos[0] = pos[0] + target.clientWidth / 2 - 0.8 * scale;
                    crosshairPos[1] = pos[1] + target.clientHeight / 2 - 0.8 * scale;
                    if (crosshairState) this.crosshair(true, crosshairPos[0], crosshairPos[1]);
                    callback(_this, pos);
                }

                document.onmouseup = (e) => {
                    target.style.cursor = 'grab';
                    if (crosshairState) this.crosshair(false);
                    document.onmousemove = () => {}
                    document.onmouseup = () => {}
                }
            }
        }

        render() {
            return (
                <Mobile.Provider value={{
                    mobile: this.state.mobile,
                    scale: this.state.scale
                }}>
                    <div id={this.props.DOMElement['Nodes']}>
                        {this.state.nodes.map((node, index) => {
                            return node? <Node
                                key={index} 
                                index={index} 
                                nodes={this.state.nodes} 
                                update={this.nodeUpdate} 
                                delete={this.nodeDelete}
                                drag={this.drag}
                                color={node.color}
                                textColor={node.textColor}
                            /> : null;
                        })}
                    </div>
                    <div id={this.props.DOMElement['Edges']}>
                        {this.state.edges.map((edge, index) => <Edge
                            key={index} 
                            index={index} 
                            delete={this.edgeDelete}
                            v1={this.state.nodes[edge[0]]} 
                            v2={this.state.nodes[edge[1]]}
                        />)}
                    </div>
                    {this.state.crosshair.enable && this.state.crosshair.display? <Crosshair
                        left={this.state.crosshair.left}
                        top={this.state.crosshair.top}
                        target={this.state.crosshair.target}
                    /> : null}
                </Mobile.Provider>
            )
        }
    }

//=================================================================================
//=================================================================================

    class Node extends React.Component {
        constructor(props) {
            super(props);
        }

        drag = (e) => {
            this.props.drag(e, e.target, this, (_this, pos) => {
                _this.props.update(this.props.index, [pos[0], pos[1]]);
            }, true)
        }
        
        render() {
            const color = this.props.color,
                  textColor = this.props.textColor;

            return (
                <button
                    className='Node'
                    style={{
                        left: this.props.nodes[this.props.index].left,
                        top: this.props.nodes[this.props.index].top,
                        backgroundColor: color? `rgb(${color[0]}, ${color[1]}, ${color[2]})`: '',
                        color: textColor? `rgb(${textColor[0]}, ${textColor[1]}, ${textColor[2]})`: ''
                    }}
                    onTouchStart={this.drag}
                    onMouseDown={this.drag}
                    onDoubleClick={(e) => {
                        e.preventDefault();
                        console.log('dd');
                        this.props.delete(this.props.index)}
                    }
                >
                    {this.props.index}
                </button> 
            )
        }
    }

//=================================================================================
//=================================================================================

    class Edge extends React.Component {
        static contextType = Mobile;

        constructor(props) {
            super(props);
        }

        render() {
            let [v1, v2] = [this.props.v1, this.props.v2];
            const [x, y] = [v1.left - v2.left, v1.top - v2.top];
            let angle = Math.atan(y / x);
            if (x >= 0) angle += Math.PI;
            angle -= Math.PI / 2;

            let scale = 1.0;
            if (this.context.mobile === true) scale *= this.context.scale;

            return (
                <div
                    className='Edge'
                    style={{
                        height: Math.sqrt(x*x + y*y),
                        left: v1.left + 27 * scale / 2 - 2 * Math.cos(angle) * scale,
                        top: v1.top + 27 * scale / 2 - 2 * Math.sin(angle) * scale,
                        transform: `rotate(${angle}rad)`
                    }}
                    onDoubleClick={() => this.props.delete(this.props.index)}
                />
            )
        }
    }

//=================================================================================
//=================================================================================

    class Crosshair extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            return (
                <div>
                    <div id="crosshairVert" style={{left: this.props.left}}/>
                    <div id="crosshairHorizontal" style={{top: this.props.top}}/>
                </div>
            )
        }
    }

//=================================================================================
//=================================================================================

    class EdgeBar extends React.Component {
        constructor(props) {
            super(props);
            let scale = 1.0;
            if (this.props.graph.state.mobile === true) scale *= this.props.graph.state.scale;

            this.state = {
                left: this.props.graph.state.mobile? document.body.clientWidth - 130 * scale : (document.body.clientWidth - 120) / 2,
                top: 10 * scale
            }
            this.v1 = React.createRef();
            this.v2 = React.createRef();
        }

        focusOut = (e, index) => {
            e.persist();

            if (e.relatedTarget && e.relatedTarget.className === "Node") {
                this[`v${index}`].current.value = e.relatedTarget.innerText;
            }
        }

        drag = (e) => {
            if (e.target.tagName !== 'INPUT') {
                this.props.graph.drag(e, document.querySelector('#edge_bar'), this, (_this, pos) => {
                    _this.setState({left: pos[0], top: pos[1]})
                })
            }
        }

        addEdge = (e) => {
            e.preventDefault();
            const res = this.props.graph.addEdge(this.v1.current.value, this.v2.current.value);
            //error
        }

        render() {
            return (
                <form
                    onMouseDown={this.drag}
                    onTouchStart={this.drag}
                    onSubmit={(e) => this.addEdge(e)}
                    id="edge_bar"
                    style={{left: this.state.left, top: this.state.top}}
                >
                    <input
                        ref={this.v1}
                        placeholder="0"
                        onFocus={() => {
                            this.v1.current.select()
                            this.v1.current.setAttribute('tabIndex', 1);
                            this.v2.current.setAttribute('tabIndex', 2);
                        }}
                        onBlur={(e) => this.focusOut(e, 1)}
                    />
                    <div
                        onClick={() => {
                            [this.v1.current.value, this.v2.current.value] = [this.v2.current.value, this.v1.current.value];
                        }}
                    >+</div>
                    <input
                        ref={this.v2}
                        placeholder="1"
                        onFocus={() => {
                            this.v2.current.select()
                            this.v2.current.setAttribute('tabIndex', 1);
                            this.v1.current.setAttribute('tabIndex', 2);
                        }}
                        onBlur={(e) => this.focusOut(e, 2)}
                    />
                    <button>З'єднати</button>
                </form>
            )
        }
    }

//=================================================================================
//=================================================================================

    class PaintButton extends React.Component {
        constructor(props) {
            super(props);
            let scale = 1.0;
            if (this.props.graph.state.mobile === true) scale *= this.props.graph.state.scale;

            this.state = {
                left: 10 * scale,
                top: 10 * scale
            }
        }

        drag = (e) => {
            this.props.graph.drag(e, e.target, this, (_this, pos) => {
                this.setState({left: pos[0], top: pos[1]});
            })
        }

        render() {
            return (
                <button
                    onMouseDown={this.drag}
                    onTouchStart={this.drag}
                    onClick={this.props.graph.paintAlgo}
                    style={{
                        left: this.state.left,
                        top: this.state.top
                    }}
                >Фарбувати</button>
            )
        }
    }

//=================================================================================
//=================================================================================

export {Graph, Node, Edge, EdgeBar, Crosshair, PaintButton};