import React from 'react';
import ReactDOM from 'react-dom';
import {Graph, Node, Edge, EdgeBar, Crosshair, PaintButton} from './App';

const graph = ReactDOM.render(<Graph DOMElement = {{
    'Nodes': 'node_container',
    'Edges': 'edge_container'
}}/>, document.querySelector('#graph_container'));

if (window.navigator.maxTouchPoints > 0) {
    document.querySelector('#graph_container').setAttribute('mobile', true); 
    document.querySelector('#edge_bar_container').setAttribute('mobile', true);
    document.querySelector('#paint_button').setAttribute('mobile', true);
    document.querySelector('#copyright').setAttribute('mobile', true);
    graph.setState({mobile: true});
}

const edgeBar = ReactDOM.render(<EdgeBar graph={graph}/>, document.querySelector('#edge_bar_container'));
const paintButton = ReactDOM.render(<PaintButton graph={graph}/>, document.querySelector('#paint_button'));

let scale = 1.0;
if (graph.state.mobile === true) scale *= graph.state.scale;

//=================================================================================
//=================================================================================

document.onmousedown = (e) => {
    if (e.altKey === true) graph.addNode([e.pageX - 27 * scale / 2, e.pageY - 27 * scale / 2]);
}

document.ondblclick = (e) => {
    e.preventDefault();
    if (e.target === document.body) graph.addNode([e.pageX - 27 * scale / 2, e.pageY - 27 * scale / 2]);
}

let longTouch;

document.ontouchstart = (e) => {
    if (e.target.tagName === 'BODY') 
        longTouch = setTimeout(() => {
            graph.addNode([e.touches[0].pageX - 27 * scale / 2, e.touches[0].pageY - 27 * scale / 2]);
        }, 250);
}

document.ontouchend = (e) => {
    clearTimeout(longTouch);
}